<template>
  <section class="page user-profile">
    <card-box
      title="我的书架"
      :iconSrc="resourceIcon"
      class="resource-info-box"
      showGap
    >
      <div class="header-tabs">
        <el-tabs
          v-model="activeBookshelfTab"
          @tab-click="switchBookshelfTab"
          :class="'tab-wrap-' + currentBookshelfTabIndex"
        >
          <el-tab-pane label="我的商品" name="my-goods">
            <div class="myResources2">
              <div
                class="myResources2_li"
                v-for="(item, index) in bookshelfModel[0].dataList"
                :key="index"
              >
                <div class="myResources2_liimgbox">
                  <el-image
                    class="myResources2_liimg"
                    @click="toread(item)"
                    :src="item.cover"
                  ></el-image>
                  <span class="scll" v-show="item.isRead === '0'"
                    >上次浏览</span
                  >
                  <img
                    v-show="
                      item.isBuy === '0' && item.acquireSource != 'lexueCode'
                    "
                    class="ygm"
                    src="@/assets/icon/ygm_ico.png"
                  />
                </div>
                <div class="myResources2_b" @click="toread(item)">
                  <span v-show="item.sourceFile">{{ item.sourceFile }}</span>
                  <p class="ellipsis-aRow" :title="item.relevanceName">
                    {{ item.relevanceName }}
                  </p>
                </div>
              </div>
              <no-data v-show="bookshelfModel[0].total === 0"></no-data>
            </div>
          </el-tab-pane>
          <el-tab-pane label="我的专题" name="my-subject">
            <div class="demo-card-list">
              <div
                class="demo-commodit-card"
                v-for="(item, index) in bookshelfModel[1].dataList"
                :key="index"
              >
                <div
                  class="demo-card-imgbox flex-center"
                  @click="tomyDetail(item)"
                >
                  <img :src="item.cover" />
                </div>
                <div class="demo-card-body-box">
                  <div class="title ellipsis-aRow" @click="tomyDetail(item)">
                    {{ item.relevanceName }}
                  </div>
                  <div class="demo-card-info">
                    <div class="resource ellipsis-aRow">
                      共{{ item.resourceNum }}个资源
                    </div>
                    <div v-if="item.isBuy === '0'" style="font-size: 14px">
                      <img
                        v-show="item.isBuy === '0'"
                        class="ygm"
                        src="@/assets/icon/ygm_ico.png"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <no-data v-show="bookshelfModel[1].total === 0"></no-data>
          </el-tab-pane>
          <el-tab-pane label="我的资源包" name="my-bundle">
            <div class="demo-card-list">
              <div
                class="demo-commodit-card"
                v-for="(item, index) in bookshelfModel[2].dataList"
                :key="index"
              >
                <div
                  class="demo-card-imgbox flex-center"
                  @click="tozybDetail(item)"
                >
                  <img :src="item.cover" />
                </div>
                <div class="demo-card-body-box">
                  <div class="title ellipsis-aRow" @click="tozybDetail(item)">
                    {{ item.relevanceName }}
                  </div>
                  <div class="demo-card-info">
                    <div class="resource ellipsis-aRow">
                      共{{ item.resourceNum }}个资源
                    </div>
                    <div v-if="item.isBuy === '0'" style="font-size: 14px">
                      <img
                        v-show="item.isBuy === '0'"
                        class="ygm"
                        src="@/assets/icon/ygm_ico.png"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <no-data v-show="bookshelfModel[2].total === 0"></no-data>
          </el-tab-pane>
        </el-tabs>
      </div>
    </card-box>
    <card-box title="订单信息" :iconSrc="orderIcon" class="order-info-box">
      <div class="header-tabs">
        <el-tabs
          stretch
          v-model="activeTab"
          :class="'tab-wrap-' + currentTabIndex"
          @tab-click="switchTab"
        >
          <el-tab-pane
            :label="` 待付款 ( ${tabModels[0].total} )`"
            name="待付款"
          >
            <div class="list-wrap">
              <order-table :orderList="tabModels[0].dataList"></order-table>
            </div>
          </el-tab-pane>
          <el-tab-pane
            :label="` 待评价 ( ${tabModels[1].total} )`"
            name="待评价"
          >
            <div class="list-wrap">
              <order-table :orderList="tabModels[1].dataList"></order-table>
            </div>
          </el-tab-pane>
          <el-tab-pane
            :label="` 已完成 ( ${tabModels[2].total} )`"
            name="已完成"
          >
            <div class="list-wrap">
              <order-table :orderList="tabModels[2].dataList"></order-table>
            </div>
          </el-tab-pane>
          <el-tab-pane
            :label="` 已取消 ( ${tabModels[3].total} )`"
            name="已取消"
          >
            <div class="list-wrap">
              <order-table :orderList="tabModels[3].dataList"></order-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </card-box>
    <el-dialog
      key="__index__"
      title="付款"
      :visible.sync="modalShow"
      width="500px"
      append-to-body
      :destroy-on-close="true"
      class="ou-modal pay-modal"
      @close="closed"
    >
      <div class="pay-modal-wrap">
        <div class="info-row">
          支付金额：<span class="price-span">{{
            (payDetail.payment || 0) | paymentText
          }}</span>
        </div>
        <div class="info-row">支付方式：</div>
        <div class="pay-way-wrap">
          <div
            class="pay-box"
            @click="selectWay('wx')"
            :class="[selectedWay === 'wx' ? 'active' : '']"
          >
            <div class="box-wrap">
              <el-image
                :src="require('@/assets/icon/wxpay.png')"
                fit="contain"
              ></el-image>
            </div>
            <el-image
              v-show="selectedWay === 'wx'"
              class="select-flag"
              :src="require('@/assets/icon/btn-pay-corner.png')"
              fit="contain"
            ></el-image>
          </div>
          <div
            class="pay-box"
            @click="selectWay('ali')"
            :class="[selectedWay === 'ali' ? 'active' : '']"
          >
            <div class="box-wrap">
              <el-image
                :src="require('@/assets/icon/alipay.png')"
                fit="contain"
              ></el-image>
            </div>
            <el-image
              v-show="selectedWay === 'ali'"
              class="select-flag"
              :src="require('@/assets/icon/btn-pay-corner.png')"
              fit="contain"
            ></el-image>
          </div>
        </div>
        <div v-show="selectedWay !== ''">
          <div class="info-row">扫码支付：</div>
          <div class="qrcode-box">
            <div id="qrcode2-index" v-show="selectedWay === 'wx'"></div>
            <div id="qrcode-index" v-show="selectedWay === 'ali'"></div>
            <div class="qrcode-tips">
              {{
                selectedWay === "wx"
                  ? "打开手机微信扫码支付"
                  : "打开手机支付宝扫码支付"
              }}
            </div>
            <div
              v-show="selectedWay === 'wx'"
              @click="refresh"
              class="qrcode-tips"
            >
              刷新
            </div>
            <div
              v-show="selectedWay === 'ali'"
              @click="refresh2"
              class="qrcode-tips"
            >
              刷新
            </div>
          </div>
        </div>
      </div>
      <!-- <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="mockPay">一键支付</el-button>
      </div> -->
    </el-dialog>
  </section>
</template>

<script>
import QRCode from "qrcodejs2";
import { aliCode, wxCode, aliOrderQuery, wxOrderQuery } from "@/api/cart/cart";
import { mapGetters, mapActions } from "vuex";
import { v4 as uuidv4 } from "uuid";
import {
  apiGetUserOrderList,
  apiGetTotalGroup,
  apiAfterPay,
} from "@/api/user/order";
import {
  selectMyResourceListGoods,
  selectMyResourceListSubject,
  selectMyResourceListBundle,
} from "@/api/user/myResources.js";
import NoData from "@/components/noData.vue";
import CardBox from "./components/CardBox";
import OrderTable from "./components/OrderTable";
import { setToken } from "@/utils/auth";
import resourceIcon from "@/assets/icon/icon-user-index-resource.png";
import orderIcon from "@/assets/icon/icon-user-index-order.png";

export default {
  name: "userProfile",
  components: { CardBox, OrderTable, NoData },
  data() {
    return {
      orderNumber: null,
      codeUrl: null,
      codeUrl2: null,
      timer1: null,
      timer2: null,
      resourceIcon,
      orderIcon,
      listquery: {
        pageNum: 1,
        pageSize: 5,
        timeBar: null,
        buyStatus: null,
        goodsType: null,
        acquireSource: null,
      },
      prevTabIndex: 0,
      activeTab: "待付款",
      tabModels: [
        {
          searchParams: {
            keyword: "",
            pageNum: 1,
            pageSize: 2,
          },
          status: 0,
          total: 0,
          dataList: [],
        },
        {
          searchParams: {
            keyword: "",
            pageNum: 1,
            pageSize: 2,
          },
          status: 1,
          total: 0,
          dataList: [],
        },
        {
          searchParams: {
            keyword: "",
            pageNum: 1,
            pageSize: 2,
          },
          status: 3,
          total: 0,
          dataList: [],
        },
        {
          searchParams: {
            keyword: "",
            pageNum: 1,
            pageSize: 2,
          },
          status: 2,
          total: 0,
          dataList: [],
        },
      ],
      modalShow: false,
      selectedWay: "wx",
      payDetail: {},

      // 2022-08-09需求变更：我的书架添加tabs页
      activeBookshelfTab: "my-goods",
      bookshelfModel: [
        {
          searchParams: {
            pageNum: 1,
            pageSize: 5,
          },
          total: 0,
          dataList: [],
        },
        {
          searchParams: {
            pageNum: 1,
            pageSize: 3,
          },
          total: 0,
          dataList: [],
        },
        {
          searchParams: {
            pageNum: 1,
            pageSize: 3,
          },
          total: 0,
          dataList: [],
        },
      ],
    };
  },
  watch: {},
  computed: {
    ...mapGetters(["userId"]),
    currentTabIndex() {
      switch (this.activeTab) {
        case "待付款":
          return 0;
        case "待评价":
          return 1;
        case "已完成":
          return 2;
        case "已取消":
          return 3;
      }
    },
    currentBookshelfTabIndex() {
      switch (this.activeBookshelfTab) {
        case "my-goods":
          return 0;
        case "my-subject":
          return 1;
        case "my-bundle":
          return 2;
      }
    },
  },
  provide() {
    return {
      refreshList: this.getCurrentTabOrderList,
      callPay: this.handlePay,
    };
  },
  filters: {
    paymentText(value) {
      let text = " - ";
      if (value || value === 0) {
        text = Number(value).toFixed(2);
      }
      return text;
    },
  },
  mounted() {},
  activated() {
    this.getMyResourceList();
    this.getCurrentTabOrderList();
    this.getStatusTotal();
  },
  beforeDestroy() {
    this.timer1 && clearInterval(this.timer1);
    this.timer2 && clearInterval(this.timer2);
  },
  methods: {
    getMyResourceList() {
      selectMyResourceListGoods(this.bookshelfModel[0].searchParams).then(
        (res) => {
          this.bookshelfModel[0].total = res.total || 0;
          this.bookshelfModel[0].dataList = res.rows;
        }
      );
    },
    getMySubjectList() {
      selectMyResourceListSubject(this.bookshelfModel[1].searchParams).then(
        (res) => {
          this.bookshelfModel[1].total = res.total || 0;
          this.bookshelfModel[1].dataList = res.rows;
        }
      );
    },
    getMyBundleList() {
      selectMyResourceListBundle(this.bookshelfModel[2].searchParams).then(
        (res) => {
          this.bookshelfModel[2].total = res.total || 0;
          this.bookshelfModel[2].dataList = res.rows;
        }
      );
    },
    toread(item) {
      console.log(item);
      if (item.sourceType == "paper") {
        let url = this.$router.resolve({
          path: "/bookstore/answer",
          query: {
            goodsId: item.relevanceId,
            relevanceId: item.relevanceId, //业务id
            resource: item.acquireSource, //来源
            resourceType: item.resourceType,
            teachInstId: item.teachInstId,
          },
        });
        window.open(url.href, "_blank");
      } else if (item.goodsType != "train" && item.sourceType != "paper") {
        let url = this.$router.resolve({
          path: "/onlineReading",
          query: {
            goodsId: item.relevanceId,
            relevanceId: item.relevanceId, //业务id
            resource: item.acquireSource, //来源
            resourceType: item.resourceType,
            typeName: item.isBuy || "", //判断是否能加入书架
            teachInstId: item.teachInstId,
          },
        });
        window.open(url.href, "_blank");
      } else if (item.goodsType == "train") {
        let url = this.$router.resolve({
          path: "/willingLearn/courseQuestions",
          query: {
            goodsId: item.relevanceId,
          },
        });
        window.open(url.href, "_blank");
      }
    },
    handlePay(detail) {
      this.payDetail = detail;
      this.modalShow = true;
      this.orderNumber = this.payDetail.orderNumber;
      this.selectedWay === "wx" && this.getWxCode();
      this.selectedWay === "ali" && this.getAliCode();
      this.selectedWay === "wx" && this.wxOrderState();
      this.selectedWay === "ali" && this.aliOrderState();
    },
    // 点击一键支付
    mockPay() {
      // TODO:
      apiAfterPay({ id: this.payDetail.id }).then(() => {
        this.modalShow = false;
        this.$message.success("订单支付成功");
        this.timer1 && clearInterval(this.timer1);
        this.timer2 && clearInterval(this.timer2);
        setTimeout(() => {
          this.getCurrentTabOrderList();
        }, 500);
      });
    },
    selectWay(value) {
      if (value === "wx") {
        this.selectedWay = "wx";
        this.getWxCode();
        clearInterval(this.timer2);
        clearInterval(this.timer1);
        this.wxOrderState();
      } else if (value === "ali") {
        this.selectedWay = "ali";
        this.getAliCode();
        clearInterval(this.timer1);
        clearInterval(this.timer2);
        this.aliOrderState();
      }
    },
    getWxCode() {
      // 获取微信二维码
      wxCode(this.orderNumber).then((res) => {
        let code = res.data ? res.data.qrCode : null;
        if (!code) {
          this.$message.error("获取支付二维码失败");
          const dom = document.getElementById("qrcode2-index");
          if (dom) dom.innerHTML = "";
          return;
        }
        this.codeUrl2 = code;
        this.$nextTick(() => {
          this.createQrcode2();
        });
      });
    },
    getAliCode() {
      // 获取支付宝二维码
      aliCode(this.orderNumber).then((res) => {
        let code = res.data ? res.data.qrCode : null;
        if (!code) {
          this.$message.error("获取支付二维码失败");
          const dom = document.getElementById("qrcode-index");
          if (dom) dom.innerHTML = "";
          return;
        }
        this.codeUrl = code;
        this.$nextTick(() => {
          this.createQrcode();
        });
      });
    },
    createQrcode() {
      // 若存在，先删除二维码
      const dom = document.getElementById("qrcode-index");
      if (dom) dom.innerHTML = "";
      // 这里会自动去取模版中的id为qrcode的
      console.log("alidom", dom, this.codeUrl);
      const qrcode = new QRCode("qrcode-index", {
        width: 150, // 宽
        height: 150, // 高
        text: this.codeUrl, // 链接地址
      });
      return qrcode;
    },
    createQrcode2() {
      // 若存在，先删除二维码
      const dom = document.getElementById("qrcode2-index");
      if (dom) dom.innerHTML = "";
      // 这里会自动去取模版中的id为qrcode的
      console.log("wxdom", dom, this.codeUrl2);
      const qrcode = new QRCode("qrcode2-index", {
        width: 150, // 宽
        height: 150, // 高
        text: this.codeUrl2, // 链接地址
      });
      return qrcode;
    },
    switchTab(e, v) {
      // console.log("切换,", e, v);
      // console.log("当前激活页及页码", this.activeTab, this.currentTabIndex);
      this.tabModels[this.prevTabIndex].searchParams.keyword = "";
      this.prevTabIndex = this.currentTabIndex;
      this.getCurrentTabOrderList();
    },
    getCurrentTabOrderList() {
      let model = this.tabModels[this.currentTabIndex];
      let {
        searchParams: { pageNum, pageSize, keyword },
        status,
      } = model;
      let params = { status, pageSize, pageNum, userId: this.userId };
      params = keyword ? { ...params, keyword } : params;
      // console.log("订单列表参数：", params);
      apiGetUserOrderList(params).then((res) => {
        // console.log("订单列表返回：", res);
        let list = res.rows || [],
          total = res.total || 0;
        this.tabModels[this.currentTabIndex].dataList = list;
        this.tabModels[this.currentTabIndex].total = total;
      });
    },
    getStatusTotal() {
      apiGetTotalGroup({ userId: this.userId }).then((res) => {
        let { allCount, waitPayCount, talkCount, closeCount, finishCount } =
          res;
        this.tabModels[0].total = Number(waitPayCount);
        this.tabModels[1].total = Number(talkCount);
        this.tabModels[2].total = Number(finishCount);
        this.tabModels[3].total = Number(closeCount);
      });
    },
    //刷新二维码
    refresh() {
      $(".qrcode").html(""); //把之前的置为空
      this.getWxCode();
    },
    //刷新二维码
    refresh2() {
      $(".qrcode").html(""); //把之前的置为空
      this.getAliCode();
    },
    // 轮询微信订单状态
    wxOrderState() {
      this.timer1 = setInterval(() => {
        wxOrderQuery(this.orderNumber, "web").then((res) => {
          if (res.code == 200) {
            clearInterval(this.timer1);
            this.modalShow = false;
            this.$message.success("订单支付成功");
            setTimeout(() => {
              this.getCurrentTabOrderList();
              this.getStatusTotal();
              this.currentBookshelfTabIndex === 0 && this.getMyResourceList();
              this.currentBookshelfTabIndex === 1 && this.getMySubjectList();
              this.currentBookshelfTabIndex === 2 && this.getMyBundleList();
            }, 800);
          }
        });
      }, 3000);
    },
    // 轮询支付宝订单状态
    aliOrderState() {
      this.timer2 = setInterval(() => {
        aliOrderQuery(this.orderNumber).then((res) => {
          if (res.code == 200) {
            clearInterval(this.timer2);
            this.modalShow = false;
            this.$message.success("订单支付成功");
            setTimeout(() => {
              this.getCurrentTabOrderList();
              this.getStatusTotal();
              this.currentBookshelfTabIndex === 0 && this.getMyResourceList();
              this.currentBookshelfTabIndex === 1 && this.getMySubjectList();
              this.currentBookshelfTabIndex === 2 && this.getMyBundleList();
            }, 800);
          }
        });
      }, 3000);
    },
    //当点击弹出框的关闭时
    closed() {
      this.timer1 && clearInterval(this.timer1);
      this.timer2 && clearInterval(this.timer2);
    },
    switchBookshelfTab(e, v) {
      e.index === "0" && this.getMyResourceList();
      e.index === "1" && this.getMySubjectList();
      e.index === "2" && this.getMyBundleList();
    },
    tomyDetail(item) {
      this.$router.push({
        path: "/special/myspecialDetail",
        query: {
          subjectId: item.id,
          resourceType: item.resourceType,
          resource: item.acquireSource,
          relevanceId: item.relevanceId, //业务id
        },
      });
    },
    tozybDetail(item) {
      this.$router.push({
        path: "/user/myresourcesDetail",
        query: {
          subjectId: item.id,
          resourceType: item.resourceType,
          resource: item.acquireSource,
          relevanceId: item.relevanceId, //业务id
          teachInstId: item.teachInstId,
        },
      });
    },
  },
};
</script>

<style scoped lang="scss">
.resource-info-box ::v-deep {
  .header-tabs {
    position: relative;
    .el-tabs__header {
      margin-bottom: 14px;
    }
    .el-tabs__nav-wrap {
      padding: 0 15px;
      height: 52px;
      border-bottom: 1px solid #f2f2f2;
    }
    .el-tabs__item {
      height: 52px;
      line-height: 52px;
      padding: 0 27px;
      &:last-child {
        padding-right: 0;
      }
      &.is-active {
        color: #2e9adb;
      }
    }
    .el-tabs__active-bar {
      background-color: #2e9adb;
      width: 90px !important;
    }
    .tab-wrap-0 {
      .el-tabs__active-bar {
        transform: translateX(10px) !important;
      }
    }
    .tab-wrap-1 {
      .el-tabs__active-bar {
        transform: translateX(119px) !important;
      }
    }
    .tab-wrap-2 {
      .el-tabs__active-bar {
        transform: translateX(237px) !important;
      }
    }
  }
  .myResources2 {
    width: 100%;
    padding: 0 30px;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
  }

  .myResources2 i {
    display: none;
  }

  .myResources2_li {
    width: 150px;
    // float: left;
    margin-right: 68px;
    margin-bottom: 20px;
  }

  .myResources2_li:nth-child(5n) {
    margin-right: 0;
  }

  .myResources2_liimgbox {
    width: 150px;
    height: 150px;
    // float: left;
    position: relative;
    margin-bottom: 15px;
  }

  .myResources2_liimg {
    cursor: pointer;
    width: 150px;
    height: 150px;
    border-radius: 6px;
    border: 1px solid #f2f2f2;
    background: #fff;
  }

  .myResources2_b {
    cursor: pointer;
    width: 100%;
    // float: left;
  }

  .myResources2_b span {
    float: left;
    margin-right: 7px;
    padding: 0 10px;
    height: 24px;
    background: #f9fdff;
    border: 1px solid #0081ce;
    border-radius: 6px;
    font-size: 13px;
    line-height: 24px;
    text-align: center;
    font-family: PingFang SC;
    font-weight: 400;
    color: #0081ce;
  }

  .myResources2_b p {
    font-size: 14px;
    font-family: PingFang SC;
    font-weight: 400;
